==============================================================================*/
html, body {
  margin: 0;
  padding: 0;
}

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

table {
  font-size: 1em;
}

*, *:before, *:after {
  box-sizing: border-box;
}

::-moz-selection {
  background: rgba(76, 175, 80, 0.25);
  text-shadow: none;
}

::selection {
  background: rgba(76, 175, 80, 0.25);
  text-shadow: none;
}

/*==============================================================================
  @Base Classes - scss/elements/_base.scss
==============================================================================*/
.clearfix:after {
  content: '';
