  font-size: 14px;
  /* [1] */
}

body {
  font-family: "Open Sans", "Helvetica Neue", "HelveticaNeue", helvetica, arial, sans-serif;
  font-size: 14px;
  line-height: 1.5em;
  font-weight: normal;
  color: #212121;
  -webkit-font-smoothing: subpixel-antialiased;
  /* [2] */
}

/**
