/**
 * Swatch
 */
.swatch {
  display: inline-block;
  border: 1px solid rgba(0, 0, 0, 0.1);
  width: 44px;
  height: 44px;
  border-radius: 3px;
}

.bg-color {
  background: #212121;
}

.bg-color-light {
  background: #9E9E9E;
}

.bg-color-dark {
  background: #212121;
}

.bg-color-heading {
  background: #212121;
}

.bg-color-link {
  background: #2196F3;
}

.bg-color-link-hover {
  background: #1976D2;
}

.bg-color-link-heading {
  background: #2196F3;
}

.bg-color-link-heading-hover {
  background: #E64A19;
}

.bg-bg-color {
  background: #F5F5F5;
}

.bg-bg-selection {
  background: rgba(76, 175, 80, 0.25);
}

.bg-border-color {
  background: rgba(0, 0, 0, 0.1);
}

.bg-border-color-hover {
  background: rgba(0, 0, 0, 0.25);
}

/**
 * Swatch and Value combo
 */
.swatch-wrap {
  position: relative;
  height: 44px;
  min-width: 44px;
}

.swatch-wrap .swatch {
  position: absolute;
  top: 0;
  left: 0;
}

.swatch-wrap .swatch-value {
  margin-left: 49px;
  display: inline-block;
  width: auto;
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

/**
 * Swatch Cards
 */
.swatch-card {
  margin: 2em 0;
}

.swatch-card ul {
  margin: 0.5em 0 0;
  list-style: none;
}

.swatch-card ul li {
  margin: 0;
}

@media (min-width: 760px) {
  .swatch-card-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0 -2em 2em 0;
  }
  .swatch-card {
    flex: 1 0 auto;
    width: 40%;
    margin: 2em 2em 0 0;
  }
}

.swatch-card .swatch {
  display: block;
  width: auto;
  height: auto;
  padding: 1em;
  border: none;
  border-radius: 0;
  color: #fff;
}

.swatch-card .swatch.text-dark {
  color: #212121;
}

.swatch-card .swatch .name {
  display: block;
  margin-bottom: 3em;
  padding-bottom: 0.5em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
  text-transform: capitalize;
}

.swatch-card .swatch.text-dark .name {
  border-color: rgba(0, 0, 0, 0.1);
}

.swatch-card .swatch .var,
.swatch-card .swatch .hex {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

.swatch-card .swatch .hex {
  float: right;
}

